import { BaseEntity, Column, CreateDateColumn, Entity, PrimaryGeneratedColumn } from "typeorm";

export enum RecordTypes {
  log = 'log',
  warn = 'warn',
  error = 'error',
  verbose = 'verbose',
  debug = 'debug'
}

/**
 * –ó–∞–ø–∏—Å—å –∂—É—Ä–Ω–∞–ª–∞ —Å–æ–±—ã—Ç–∏–π
 */
@Entity()
export class EventLoggerRecordEntity extends BaseEntity {
  @PrimaryGeneratedColumn()
  id: number;

  @CreateDateColumn()
  timestamp: string;

  @Column({ type: 'varchar', nullable: false, enum: RecordTypes })
  type: RecordTypes;

  @Column({ type: 'varchar', nullable: false })
  message: string;

  @Column({ type: 'varchar', nullable: true })
  context: string | null;

  
  /**
   * –î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å –≤ –∂—É—Ä–Ω–∞–ª —Å–æ–±—ã—Ç–∏–π
   * @param type –¢–∏–ø –∑–∞–ø–∏—Å–∏
   * @param message –°–æ–æ–±—â–µ–Ω–∏–µ
   * @param context –ö–æ–Ω—Ç–µ–∫—Å—Ç –≤ –∫–æ—Ç–æ—Ä–æ–º –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –º–µ—Ç–æ–¥
   * @returns 
   */
  static async addRecord(type: RecordTypes, message: string, context?: string): Promise<EventLoggerRecordEntity> {
    let record = new EventLoggerRecordEntity();
    record.type = type;
    record.message = message;
    record.context = context ?? null;
    record = await this.save(record);
    return record;
  }
}