import {
  BaseEntity,
  Column,
  CreateDateColumn,
  Entity, ManyToMany,
  OneToOne,
  PrimaryColumn,
  UpdateDateColumn,
  JoinTable
} from "typeorm";
import { IsBoolean, IsEmail, IsOptional, IsString, Length, validate } from "class-validator";
import { BadRequestException, ForbiddenException, Logger, UnauthorizedException } from "@nestjs/common";
import { UserPasswordEntity } from "./user-password.entity";
import { UserInterface } from "../../../../common/interfaces/src";
import { RoleEntity } from "./role.entity";


@Entity('Users')
export class UserEntity extends BaseEntity {
  @IsEmail()
  @Length(1, 255)
  @PrimaryColumn({ type: 'varchar', length: 255, unique: true })
  email: string;

  @IsString()
  @Length(1, 255)
  @Column({ type: 'varchar', length: 255, unique: true, nullable: false })
  name: string;

  @IsOptional()
  @IsBoolean()
  @Column({ type: 'boolean', nullable: false, default: true })
  isActive: boolean;

  @CreateDateColumn()
  createTimestamp: Date;

  @UpdateDateColumn()
  updateTimestamp: Date;

  @OneToOne(() => UserPasswordEntity, password => password.user)
  password: UserPasswordEntity;

  @ManyToMany(() => RoleEntity, roles => roles.users)
  @JoinTable()
  roles: RoleEntity[];


  /**
   * –°–æ–∑–¥–∞–Ω–∏–µ –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   * @param email
   * @param name
   * @param password
   */
  static async createUser(email: string, name: string, password: string): Promise<UserEntity> {
    Logger.log(`–°–æ–∑–¥–∞–Ω–∏–µ –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è.`, `UserEntity.createUser(${email}, ${name})`);
    const checkEmail = await this.findOne({ where: { email: email }});
    if (checkEmail) {
      Logger.warn(`${email} –∑–∞–Ω—è—Ç!`, `UserEntity.createUser(${email}, ${name})`);
      throw new BadRequestException(`${email} –∑–∞–Ω—è—Ç!`);
    }
    const countUsersWithSameName = await this.count({ where: { name: name }});
    let user = new UserEntity();
    user.email = email;
    user.name = !countUsersWithSameName ? name : `${name} - ${countUsersWithSameName + 1}`;
    const validateUserDataErrors = await validate(user);
    if (validateUserDataErrors.length) {
      Logger.warn(`${validateUserDataErrors}`, `UserEntity.createUser(${email}, ${name})`);
      throw new BadRequestException(validateUserDataErrors);
    }
    user = await this.save(user);
    try {
      await UserPasswordEntity.setPassword(user, password);
    } catch (err) {
      Logger.warn(`–ü–∞—Ä–æ–ª—å –Ω–µ –ø—Ä–æ—à–µ–ª –≤–∞–ª–∏–¥–∞—Ü–∏—é! –°–æ–∑–¥–∞–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –ø—Ä–µ—Ä–≤–∞–Ω–æ!`, `UserEntity.createUser(${email}, ${name})`);
      await this.delete(user);
      throw new BadRequestException(err);
    }

    // –ï—Å–ª–∏ —ç—Ç–æ –ø–µ—Ä–≤—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è - –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –Ω–æ–≤—ã–µ —Ä–æ–ª–∏ –∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è-–∞–¥–º–∏–Ω–∞
    const currentUsersCount = await this.count();
    if (currentUsersCount === 1) {
      Logger.log(`–°–æ–∑–¥–∞–Ω –ø–µ—Ä–≤—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è.`, `UserEntity.createUser(${email}, ${name})`);
      Logger.log(`–ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º —Ä–æ–ª–µ–≤—É—é –º–æ–¥–µ–ª—å –∏ –Ω–∞–∑–Ω–∞—á–∞–µ–º –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞!`, `UserEntity.createUser(${email}, ${name})`);
      let roleAdmin = new RoleEntity();
      roleAdmin.title = 'admin';
      roleAdmin = await RoleEntity.save(roleAdmin);
      Logger.log(`–°–æ–∑–¥–∞–Ω–∞ —Ä–æ–ª—å "–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä".`, `UserEntity.createUser(${email}, ${name})`);

      const roleUser = new RoleEntity();
      roleUser.title = 'user';
      await RoleEntity.save(roleUser);
      Logger.log(`–°–æ–∑–¥–∞–Ω–∞ —Ä–æ–ª—å "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å".`, `UserEntity.createUser(${email}, ${name})`);

      user.roles = [roleAdmin];
      await this.save(user);
      Logger.log(`–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é ${name} –ø—Ä–∏—Å–≤–æ–µ–Ω–∞ —Ä–æ–ª—å "–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä".`, `UserEntity.createUser(${email}, ${name})`);
    }

    const roleUser = await RoleEntity.findOne({ where: { title: 'user' }});
    user.roles = [...user.roles, roleUser];
    await this.save(user);
    Logger.log(`–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é ${name} –ø—Ä–∏—Å–≤–æ–µ–Ω–∞ —Ä–æ–ª—å "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å".`, `UserEntity.createUser(${email}, ${name})`);
    Logger.log(`–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å ${name} —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω.`, `UserEntity.createUser(${email}, ${name})`);
    return user;
  }


  /**
   * –ü–æ–ª—É—á–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –ø–æ email
   * @param email
   */
  static async getUserByEmail(email: string): Promise<UserInterface> {
    const entity = await this.findOne({where: {email: email}});
    if (!entity) {
      throw new UnauthorizedException();
    }
    if (!entity?.isActive) {
      throw new ForbiddenException();
    }
    return {
      email: entity.email,
      name: entity.name,
    };
  }
}
