import { ForbiddenException, Injectable, UnauthorizedException } from "@nestjs/common";
import { UserInterface } from "@znode/common/interfaces";
import { UserEntity, UserPasswordEntity } from "@znode/storage";
import { JwtService } from "@nestjs/jwt";


export interface Token {
  user: UserInterface;
  iat: number;
  exp: number;
}


@Injectable()
export class AuthService {
  constructor(private jwtService: JwtService) {}


  /**
   * –ü—Ä–æ–≤–µ—Ä–∫–∞ –∞–¥—Ä–µ—Å–∞ —ç–ª–µ–∫—Ç—Ä–æ–ø–æ—á—Ç—ã –∏ –ø–∞—Ä–æ–ª—è
   * @param email
   * @param password
   */
  public async checkEmailAndPassword(email: string, password: string): Promise<UserInterface> {
    const user = await UserEntity.findOne({ where: { email: email }});
    if (!user) {
      throw new UnauthorizedException(`${email} –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω!`, `AuthLocalStrategy.validate()`);
    }
    const checkPassword = await UserPasswordEntity.isPasswordOfUser(user, password);
    if (!checkPassword) {
      throw new UnauthorizedException(`–ù–µ–≤–µ—Ä–Ω—ã–π –ø–∞—Ä–æ–ª—å!`, `AuthLocalStrategy.validate()`);
    }
    if (!user.isActive) {
      throw new ForbiddenException(`–î–æ—Å—Ç—É–ø –∑–∞–ø—Ä–µ—â–µ–Ω!`, `AuthLocalStrategy.validate()`);
    }
    return user;
  }


  /**
   * –ó–∞—à–∏—Ñ—Ä–æ–≤–∞—Ç—å —Ç–æ–∫–µ–Ω JWT
   * @param user
   */
  public encryptJwt(user: UserInterface): string {
    const payload = { user };
    return this.jwtService.sign(payload);
  }


  /**
   * –†–∞—Å—à–∏—Ñ—Ä–æ–≤–∞—Ç—å —Ç–æ–∫–µ–Ω JWT
   * @param token
   */
  public decryptJwt(token: string): Token {
    return this.jwtService.verify<Token>(token);
  }
}
